import { Component, OnInit, ChangeDetectorRef, AfterViewChecked } from '@angular/core';
import { DatePipe } from '@angular/common';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

import { SalesComponent } from 'src/app/sales/components/bill-detail/sales/sales.component';
import { WarrantyComponent } from 'src/app/sales/components/bill-detail/warranty/warranty.component';
import { CustomerReturnComponent } from 'src/app/sales/components/bill-detail/customer-return/customer-return.component';
import { SalesVoucherComponent } from 'src/app/sales/components/bill-detail/sales-voucher/sales-voucher.component';
import { SalesPaymentCardComponent } from 'src/app/sales/components/bill-detail/sales-payment-card/sales-payment-card.component';
@Component({
  selector: 'app-detail-product',
  templateUrl: './detail-product.component.html',
  styleUrls: ['./detail-product.component.scss']
})
export class DetailProductComponent {
  product: any;
  reportConfig: any = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_employee;
  stateObj: any;
  employee: any;
  page2Detail: any;
  details: any = { quantity: 0, sales_atax: 0, sales_btax: 0, docs: [] };
  constructor(
    private router: Router,
    public languageService: LanguageService,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private datePipe: DatePipe,
    private cdRef: ChangeDetectorRef,
    private vhComponent: VhComponent,
    private vhAuth: VhAuth
  ) {
    let data: any = this.router.getCurrentNavigation().extras.state
    if (data) {
      this.product = data.product;
      this.employee = data.employee;
      this.stateObj = data.stateObj;
      this.page2Detail = data.page2Details
      if (data.page3Details) this.details = data.page3Details
      else this.getSalesDetail(this.product);
    }
  }
  heightScroll: string;
  widthScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector(".height-layout") && document.querySelector(".height-header") && document.querySelector(".ant-table-thead")) {
      this.heightScroll = (document.querySelector(".height-layout").clientHeight -
        document.querySelector(".height-header").clientHeight -
        document.querySelector(".ant-table-thead").clientHeight) - 90 + "px";
    }
    if (document.querySelector(".height-layout")) {
      this.widthScroll = document.querySelector(".height-layout").clientWidth + "px";
    }
    this.cdRef.detectChanges();
  }
  getSalesDetail(product) {
    
    if (product.ptype) {
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
        this.vhQuerySales.getReportSales_Employees_byProduct(this.stateObj.dataReport['startTime'], this.stateObj.dataReport['endTime'], product.id_employee, product.id_subproduct ? product.id_subproduct : product.id_product, this.stateObj.id_branch_report)
          .then((res) => {
            this.details = { ...res }
          }).catch(err => console.error(err))
          .finally(() => this.vhComponent.hideLoading(0))
      })
    }
    if (product.id_payment_card) {
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
        this.vhQuerySales.getReportSales_Employees_byPaymentCard(this.stateObj.dataReport['startTime'], this.stateObj.dataReport['endTime'], product.id_employee, product.id_payment_card, this.stateObj.id_branch_report)
          .then((res) => {
            this.details = { ...res }
          }).catch(err => console.error(err))
          .finally(() => this.vhComponent.hideLoading(0))
      })
    }

    if (product.id_voucher) {
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
        this.vhQuerySales.getReportSales_Employees_byVoucher(this.stateObj.dataReport['startTime'], this.stateObj.dataReport['endTime'], product.id_employee, product.id_voucher, this.stateObj.id_branch_report)
          .then((res) => {
            this.details = { ...res }
          }).catch(err => console.error(err))
          .finally(() => this.vhComponent.hideLoading(0))
      })
    }

  }

  goToDetail(item) {
    switch (item.bill_type) {
      case 1: {
        return this.vhComponent.showModal(SalesComponent, { id_bill: item.id_bill }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 3: {
        return this.vhComponent.showModal(CustomerReturnComponent, { id_bill: item.id_bill }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 15: {
        return this.vhComponent.showModal(WarrantyComponent, { id_bill: item.id_bill }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 40: {
        return this.vhComponent.showModal(SalesVoucherComponent, { id_bill: item.id_bill }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 50: {
        return this.vhComponent.showModal(SalesPaymentCardComponent, { id_bill: item.id_bill }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
    }
  }


  goBack() {
    this.router.navigate(['/sales/dashboard/manage/reports/sales-employee/detail-employee'],
      { state: { employee: this.employee, stateObj: this.stateObj, details: this.page2Detail } });
  }

  onCurrentPageDataChange(value) {
  }
  /**--------------------- */
  format_date(date) {
    return this.datePipe.transform(date, "dd_MM_yyyy")
  }


  status: boolean = false;
  export() {
    this.status = true;
    let data = [];

    let name = `${this.languageService.translate("_product_sales_report")}_${this.vhAlgorithm.changeAlias(this.product.name).replace(/\s/g, "_")}_${this.languageService.translate("_from")}_${this.format_date(this.stateObj.startTime)}_${this.languageService.translate("_to")}_${this.format_date(this.stateObj.endTime)}`

    let total: any = new Object();
    total[this.languageService.translate("Invoice code")] = "";
    total[this.languageService.translate("Date")] = "";
    total[this.languageService.translate("Product name")] = this.languageService.translate("Total");
    total[this.languageService.translate("Unit")] = "";
    total[this.languageService.translate("Quantity")] = this.details['quantity']
    total[this.languageService.translate("_price")] = "";
    total[`${this.languageService.translate("Tax")} (%)`] = "";
    if (this.reportConfig)
      total[this.languageService.translate("Sales before tax")] = this.details['sales_btax']
    total[this.languageService.translate("Sales after tax")] = this.details['sales_atax']
    total[this.languageService.translate("Employee")] = "";
    total[this.languageService.translate("Barcode")] = "";
    total[this.languageService.translate("Customer")] = "";
    data.push(total);

    for (let _rows of this.details['docs']) {
      let item: any = new Object();
      item[this.languageService.translate("Invoice code")] = _rows.bill_code;
      item[this.languageService.translate("Date")] = this.format_date(_rows.date);
      item[this.languageService.translate("Product name")] = _rows.lots ? _rows.name +' :' + _rows.lots[0].lot_number + ' :' + this.format_date(_rows.lots[0].date_mfg) + ' - ' +this.format_date(_rows.lots[0].date_exp) : _rows.name
      item[this.languageService.translate("Unit")] = _rows.unit;
      item[this.languageService.translate("Quantity")] = _rows.quantity
      item[this.languageService.translate("_price")] = _rows.price;
      item[`${this.languageService.translate("Tax")} (%)`] = _rows.tax;
      if (this.reportConfig)
        item[this.languageService.translate("Sales before tax")] = _rows.sales_btax
      item[this.languageService.translate("Sales after tax")] = _rows.sales_atax
      item[this.languageService.translate("Employee")] = _rows.salesman;
      item[this.languageService.translate("Barcode")] = _rows.barcode;
      item[this.languageService.translate("Customer")] = _rows.customer == "Retail customer" ? this.languageService.translate(_rows.customer) : _rows.customer
      data.push(item);
    }
    this.vhAlgorithm.exportXLSX(data, name).then(() => this.status = false).catch(err => this.status = false)
  }

}
